<template>
  <div
    class="douban-container"
    :style="{background: 'center url('+ backgroundPic +') no-repeat' }">
    <div class="list-container">
      <list-item
        v-for="(item, index) in doubanList"
        :key="index"
        :item="item"
      >
      </list-item>
    </div>
    <div class="input-container">
      <input type="number" class="input" placeholder="小组id" v-model="groupId">
      <button class="btn-fetch" @click="fetchData">刷新</button>
    </div>
    <div class="demo-info">
      <p><span>获取豆瓣小组的最新回复</span><br>使用Koa2转发豆瓣接口解决跨域问题
        <br>豆瓣的图片不允许陌生域名访问，使用默认图代替
        <br>自己的接口没配置https，请到浏览器设置中将不
        <br>安全内容设为'允许'
        <br>还有太多地方需要优化。。。</p>
    </div>
  </div>
</template>
<script>
  import ListItem from './../components/list-item'
  import { PicUrl, API } from '../congfig'
  export default{
    name:'douban',
    components:{
      ListItem,
    },
    data(){
      return {
        groupId: '638298', // 默认哈组
        backgroundPic: PicUrl.douban,
        doubanList:[],
      }
    },
    mounted(){
      this.fetchData();
    },
    methods:{
      fetchData(){
        if (this.doubanList.length > 0) {
          this.doubanList.splice(0, this.doubanList.length)
        }
        this.axios.get(`${API.douban.group}${this.groupId}`,{
          headers: {
            referer: 'https://www.douban.com/group/explore',
            host: 'www.douban.com'
          }
        })
          .then(res=>{
            let topics = res.data.topics
            if (!topics || topics.length === 0) {
              this.$message.error('小组Id不可用');
            }
            topics.forEach(topic => {
              let title = topic.title
              let url = topic.alt
              // let imgUrl = topic.photos.length === 0 ? '' : topic.photos[0].alt
              let imgUrl = 'http://img1.imgtn.bdimg.com/it/u=2773516379,1651914438&fm=26&gp=0.jpg'
              let extraText = '评论数：' + topic.comments_count
              this.doubanList.push({ title, url, imgUrl, extraText })
            })
        })
      },
    }
  }
</script>

<style lang="scss">
  @import "../assets/mixin";
  @import "../assets/config.scss";
  .douban-container {
    @include wh();
    position: relative;
    .list-container {
      @include positionTL(absolute, 15vh, 50%);
      height: 80vh;
      overflow: auto;
      transform: translate(-30%);
      @include scrollBar(4px, 80vh, transparent, $c_primary)
    }
    .input-container {
      @include positionTL(absolute, 25vh, 5vw);
      @include flex();
      flex-direction: row;
      align-items: flex-end;
      .input {
        @include wh(100px, 40px);
        background-color: transparent;
        border: 2px solid #fff;
        outline-style: none;
        color: $c_white;
        font-size: $font20;
      }
      .btn-fetch {
        @include wh(80px, 40px);
        margin-left: 20px;
        background-color: transparent;
        color: $c_white;
        padding: 5px;
        font-size: $font20;
        border: 2px solid #fff;
        cursor: pointer;
      }
    }
    .demo-info {
      @include positionTL(absolute, 40vh, 5vw);
      color: $c_white;
      font-size: 18px;
      line-height: 40px;
      &>p>span {
        font-size: 18px;
        font-weight: bold;
      }
    }
  }
</style>
